.card {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  width: $card-width;
  padding: 32px 16px 16px;

  background: white;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  box-sizing: border-box;

  &__image {
    display: block;
    height: 134px;
  }

  &__text {
    margin: 0;
  }

  &__text--1 {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    margin-top: 40px;

    color: $color-navy-blue;
  }

  &__text--2 {
    font-weight: 400;
    font-size: 10px;
    line-height: 14px;
    margin-top: 4px;

    color: $color-light-gray;
  }

  &__rate {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 16px;
    width: 100%;
  }

  &__rate-review {
    font-weight: 400;
    font-size: 10px;
    line-height: 18px;

    display: flex;
    text-align: right;
    justify-content: flex-end;

    color: $color-navy-blue;
  }

  &__price {
    display: flex;

    margin-top: 24px;
    margin-bottom: 0;

    width: 100%;

    align-items: flex-end;
    flex-grow: 0;
    flex-direction: row;
    justify-content: space-between;

    line-height: 18px;
  }

  &__price-text {
    height: 18px;

    font-weight: 400;
    font-size: 12px;
    color: $color-light-gray;
  }

  &__price-num {
    height: 18px;

    font-weight: 700;
    font-size: 16px;

    justify-content: flex-end;
    text-align: right;
    color: $color-navy-blue;
  }

  &__link {
    text-decoration: none;
    display: flex;
    justify-content: center;
    margin-top: 16px;

    align-items: center;
    text-transform: uppercase;

    width: 100%;
    height: 40px;
    line-height: 16px;
    text-align: center;

    background: $color-blue;
    border: 1px solid $color-blue;
    border-radius: 5px;

    color: #fff;
    font-weight: 700;
    font-size: 14px;

    box-sizing: border-box;
  }

  &__link:hover {
    background: #fff;
    color: $color-blue;
  }
}
